<template>
  <el-form ref="form"
           :model="query"
           label-width="80px">
    <el-form-item label="旧密码">

      <el-input v-model="query.oldpassword"
                show-password></el-input>
    </el-form-item>
    <el-form-item label="新密码">
      <el-input v-model="query.newpassword"
                show-password></el-input>
    </el-form-item>

    <el-form-item>
      <el-button type="primary"
                 @click="onSubmit">保存</el-button>
      <el-button>取消</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data () {
    return {
      api_token: '',
      query: {
        oldpassword: '',
        newpassword: '',
        api_token: ''
      }


    }
  },
  mounted () {
    if (window.sessionStorage.getItem('apiToken')) {
      this.api_token = window.sessionStorage.getItem('apiToken')
    } else {
      this.$router.push('login')
    }

  },
  methods: {
    onSubmit () {
      this.query.api_token = this.api_token
      this.$API.updatePassword(this.query).then(
        res => {
          //this.userInfo = res.data.data[0]
        }
      ).catch(err => { console.log(err) })
    }


  }
}
</script>


