<template>
  <el-form ref="form"
           :model="userInfo"
           label-width="80px">
    <el-form-item label="用户名">

      <el-input v-model="userInfo.username"></el-input>
    </el-form-item>
    <el-form-item label="E-mail">
      <el-input v-model="userInfo.email"></el-input>
    </el-form-item>
    <el-form-item label="电话号码">
      <el-input v-model="userInfo.phoneNum"></el-input>
    </el-form-item>

    <el-form-item label="是否公开"
                  style="text-align:left">

      <el-switch v-model="userInfo.isPublic"></el-switch>
    </el-form-item>

    <el-form-item label="简介">
      <el-input type="textarea"
                :rows="2"
                placeholder="请输入内容"
                v-model="userInfo.summary">
      </el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary"
                 @click="onSubmit">保存</el-button>
      <el-button>取消</el-button>
    </el-form-item>
  </el-form>

</template>


<script>
export default {
  data () {
    return {
      api_token: '',
      userInfo: {
        username: '',
        email: '',
        phoneNum: '',
        isPublic: '',
        summary: '',
        api_token: ''
      },

    }
  },
  mounted () {
    if (window.sessionStorage.getItem('apiToken')) {
      this.api_token = window.sessionStorage.getItem('apiToken')
    } else {
      this.$router.push('login')
    }
    this.getMyInfo()
  },
  methods: {
    onSubmit () {
      this.userInfo.api_token = this.api_token
      this.$API.EditMyInfo(this.userInfo).then(
        res => {
          // getMyInfo()
        }
      ).catch(err => { console.log(err) })
    },

    getMyInfo () {
      this.$API.getMyInfo({ api_token: this.api_token }).then(
        res => {
          this.userInfo.username = res.data.data[0].name
          this.userInfo.email = res.data.data[0].email
          this.userInfo.phoneNum = res.data.data[0].phoneNum
          this.userInfo.isPublic = res.data.data[0].isPublic === 1 ? true : false
          this.userInfo.summary = res.data.data[0].summary
        }
      ).catch(err => { console.log(err) })
    }
  }
}
</script>


