<template>
  <div>
    <tableAdd :columns="columns"
              :data="data"
              :fn="['add','del']"
              @add="CreateBlogType"
              @del="DelBlogType"></tableAdd>
  </div>
</template>


<script>
import tableAdd from "../components/Table"
export default {
  components: {
    tableAdd

  },
  mounted () {
    if (window.sessionStorage.getItem('apiToken')) {
      this.api_token = window.sessionStorage.getItem('apiToken')
    } else {
      this.$router.push('login')
    }
    this.getType()

  },
  data () {
    return {
      columns: [{
        nameC: '类型名',
        nameE: 'name'
      }, {
        nameC: '备注',
        nameE: 'remark'
      }],
      data: [],
      api_token: ''
    }
  },
  methods: {
    getType () {
      this.$API.getUBlogType({ api_token: this.api_token }).then(
        res => {
          this.data = res.data.data
        }
      ).catch(err => { console.log(err) })

    },
    CreateBlogType (data, callback) {

      data.api_token = this.api_token

      this.$API.CreateBlogType(data).then(res => {
        if (res.data.msg_code == 0) {
          callback(true)
        } else {
          callback(false)
        }


      }
      ).catch(callback(false))
    },
    DelBlogType (data, callback) {
      console.log(data.ublogTypeOnlyId)
      this.$API.DeluBlogType({
        api_token: this.api_token
        , ublogTypeOnlyId: data.ublogTypeOnlyId
      }).then(res => {
        if (res.data.msg_code == 0) {
          callback(true)
        } else {
          callback(false)
        }


      }
      ).catch(callback(false))
    }
  }
}

</script>


