<template>
  <!-- 在elment table的基础上封装有增加列功能的表格 -->

  <div>
    <template>
      <el-table :data="tableData"
                border
                style="width: 100%">

        <el-table-column width="60"
                         :render-header="renderHeader">
          <template slot-scope="scope">

            <div class="ibtn">

              <i class="el-icon-delete"
                 v-show="delSate"
                 @click.prevent="deleteRow(scope.$index,  scope.row)"></i>
              <i class="el-icon-check"
                 v-show="scope.row.showSaveicon"
                 @click.prevent="saveRow(scope.$index, scope.row)"></i>
            </div>

          </template>
        </el-table-column>

        <el-table-column prop="NameEng"
                         :label="column.nameC"
                         v-for="column in columnsArray"
                         :key="column.nameE">

          <template slot-scope="scope">
            <el-input v-model="scope.row[column.nameE]"></el-input>

          </template>
        </el-table-column>

      </el-table>

      <el-button v-show="saveState"> 保存</el-button>
    </template>
  </div>
</template>


    <script>
export default {
  data () {
    this.data.forEach(e => { e.showSaveicon = false })

    return {
      // tableData: [{
      //   NameEng: "BookName",
      //   NameChinese: "书籍名称",
      //   Type: '1',
      //   Descrip: '此为测试，生成一个书籍查询页面'
      // }],
      columnsArray: this.columns,
      tableData: this.data,
      addBtnState: 'none',//添加按钮显示
      saveState: false,//保存整个表格的按钮是否启用
      rowSaveState: false, //所有单行保存按钮是否启用，和上者二选一
      delSate: false
    }
  },

  props: {
    columns: {
      type: Array,
      default () {
        return [{
          nameC: '姓名',
          nameE: 'name',
          width: 180
        }]
      }
    },
    data: {
      type: Array,
      default () {
        return [{
          name: '小明',

        }]
      }
    },
    //功能：新增，删除，修改 （可选其中一项或多项）
    fn: {
      type: Array,
      default () {
        return ['add', 'del', 'update']
      }
    }
  },

  mounted () {

    if (this.fn.indexOf('add') >= 0) {
      this.addBtnState = "inline-block";
      this.rowSaveState = true

    }
    if (this.fn.indexOf('update') >= 0) {
      this.rowSaveState = false
      this.saveState = true
    }
    if (this.fn.indexOf('del') >= 0) {
      this.delSate = true
    }

  },
  watch: {
    rowSaveState (newVal, oldVal) {
      if (!newVal) {
        this.tableData.forEach(e => { e.showSaveicon = false })
      }

    },
    data (newVal, oldVal) {
      this.tableData = newVal
    }
  },

  methods: {
    renderHeader (h, { column }) { // h即为cerateElement的简写，具体可看vue官方文档
      return h(
        'div',
        {
          attrs: {
            id: 'addBtn'
          },
          style: {
            display: this.addBtnState
          }
        },
        [
          h('span', column.label),
          h('i', {
            class: 'el-icon-plus',
            style: 'color:#409eff;margin-left:5px;cursor:pointer',
            on: {
              click: () => {
                this.addRow()
              }
            }
          })
        ],
      );
    },
    addRow () {
      class H {


        constructor(name, remark, showSaveicon) {
          this.name = name
          this.remark = remark
          this.showSaveicon = showSaveicon
        }
      }
      console.log(this.rowSaveState)
      this.tableData.push(new H('', '', this.rowSaveState))
    },
    deleteRow (index, data) {
      this.$emit('del', data)
    },
    saveRow (index, data) {
      var paramData = {}
      Object.keys(data).forEach(e => {
        if (e != "showSaveicon") {
          paramData[e] = data[e]
        }
      })

      console.log(paramData, 'paramData')
      this.$emit('add', paramData, (res) => {
        if (res) {
          //document.getElementsByClassName("el-icon-check")[index].style.display = 'none'
          this.tableData[index].showSaveicon = false
        }

      })
    }

  }
}


    </script>

<style lang='less' scoped >
.el-input__inner {
  border: none !important;
}

.ibtn {
  cursor: pointer;
  i:first-child {
    &:hover {
      color: #f56c6c;
    }
  }
  i:last-child {
    &:hover {
      color: #67c23a;
    }
  }
}
</style>
 


