<template>
  <div>
    <pageTop title="我的"></pageTop>
    <el-container>

      <el-main>
        <el-tabs tab-position="left"
                 v-model="tabNow"
                 class="tabs">
          <el-tab-pane name='a'>
            <span slot="label"><i class="el-icon-position"></i> 浏览历史</span>
            <div class="infinite-list-wrapper"
                 style="overflow:auto">
              <ul class="list"
                  v-infinite-scroll="load"
                  infinite-scroll-disabled="disabled">
                <li v-for="i in count"
                    class="list-item"
                    :key="i">{{ i }}</li>
              </ul>
              <p v-if="loading">加载中...</p>
              <p v-if="noMore">没有更多了</p>
            </div>
          </el-tab-pane>
          <el-tab-pane name='b'>

            <span slot="label"><i class="el-icon-s-custom"></i>用户信息</span>
            <div>
              <userInfo></userInfo>
            </div>

          </el-tab-pane>
          <el-tab-pane name='c'>
            <span slot="label"><i class="el-icon-edit"></i>修改密码</span>
            <updatePassword></updatePassword>
          </el-tab-pane>
          <el-tab-pane>
            <span slot="label"><i class="el-icon-setting"></i>博客类型</span>
            <blogType></blogType>
          </el-tab-pane>
        </el-tabs>
      </el-main>
    </el-container>
  </div>
</template>


<script>
import top from '../components/top'
import userInfo from './userInfo'
import updatePassword from './updatePassword'
import blogType from './blogType'
export default {
  components: {
    pageTop: top,
    userInfo,
    updatePassword,
    blogType

  },
  data () {
    return {
      count: 10,
      loading: false,
      api_token: '',
      tabNow: 'b'
    }
  },
  mounted () {
    if (window.sessionStorage.getItem('apiToken')) {
      this.api_token = window.sessionStorage.getItem('apiToken')
    } else {
      this.$router.push('login')
    }
  },
  computed: {
    noMore () {
      return this.count >= 20
    },
    disabled () {
      return this.loading || this.noMore
    }
  },
  methods: {
    load () {
      this.loading = true
      // setTimeout(() => {
      //   this.count += 2
      //   this.loading = false
      // }, 2000)
      console.log(this.api_token)
      this.$API.getBlogHistory({ api_token: this.api_token }).then(
        res => {

        }
      ).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

<style lang="less" scoped>
.tabs {
}
</style>





